import __var_0__ from '@stdlib/constants/math/int8-min';
import __var_1__ from '@stdlib/constants/math/int16-min';
import __var_2__ from '@stdlib/constants/math/int32-min';
import __var_3__ from '@stdlib/constants/math/float16-ninf';
import __var_4__ from '@stdlib/constants/math/float32-ninf';
import __var_5__ from '@stdlib/constants/math/float64-ninf';
